/* global window                                                    */
/* global document                                                  */

'use strict'

;(function (window, document) {
  'use strict';

  var options = {
    searchInput: null,
    resultsContainer: null,
    json: [],
    searchResultTemplate: '<li><a href="{url}" title="{desc}">{title}</a></li>',
    templateMiddleware: function () {},
    noResultsText: 'No results found',
    limit: 10,
    fuzzy: false,
    exclude: []
  };

  var requiredOptions = ['searchInput', 'resultsContainer', 'json'];

  var templater = require('./Templater');
  var repository = require('./Repository');
  var jsonLoader = require('./JSONLoader');
  var optionsValidator = require('./OptionsValidator')({
    required: requiredOptions
  });
  var utils = require('./utils');

  /*
    Public API
  */
  window.SimpleJekyllSearch = function SimpleJekyllSearch (_options) {
    var errors = optionsValidator.validate(_options);
    if (errors.length > 0) {
      throwError('You must specify the following required options: ' + requiredOptions);
    }

    options = utils.merge(options, _options);

    templater.setOptions({
      template: options.searchResultTemplate,
      middleware: options.templateMiddleware
    });

    repository.setOptions({
      fuzzy: options.fuzzy,
      limit: options.limit
    });

    if (utils.isJSON(options.json)) {
      initWithJSON(options.json);
    } else {
      initWithURL(options.json);
    }
  };

  // for backwards compatibility
  window.SimpleJekyllSearch.init = window.SimpleJekyllSearch;

  if (typeof window.SimpleJekyllSearchInit === 'function') {
    window.SimpleJekyllSearchInit.call(this, window.SimpleJekyllSearch);
  }

  function initWithJSON (json) {
    repository.put(json);
    registerInput();
  }

  function initWithURL (url) {
    jsonLoader.load(url, function (err, json) {
      if (err) {
        throwError('failed to get JSON (' + url + ')');
      }
      initWithJSON(json);
    });
  }

  function emptyResultsContainer () {
    options.resultsContainer.innerHTML = '';
  }

  function appendToResultsContainer (text) {
    options.resultsContainer.innerHTML += text;
  }

  function registerInput () {
    options.searchInput.addEventListener('keyup', function (e) {
      var key = e.which;
      if (isWhitelistedKey(key)) {
        emptyResultsContainer();
        var query = e.target.value;
        if (isValidQuery(query)) {
          render(repository.search(query));
        }
      }
    });
  }

   /* jadams, 2017-07-09: added resultsOutput for J1 Bootsnav
      ------------------------------------------------------------- */
  function render (results) {
    if (results.length === 0) {
      // jadams, 2017-07-09: resultsOutput hardcoded
      var resultsOutput = document.getElementById('jss-panel');
      resultsOutput.style.display = 'none'; // to hide
      return appendToResultsContainer(options.noResultsText);
    }
    for (var i = 0; i < results.length; i++) {
      appendToResultsContainer(templater.compile(results[i]));
    }
  }

  function isValidQuery (query) {
    return query && query.length > 0;
  }

  function isWhitelistedKey (key) {
    return [13, 16, 20, 37, 38, 39, 40, 91].indexOf(key) === -1;
  }

  function throwError (message) { throw new Error('SimpleJekyllSearch --- ' + message); }
})(window, document);



/* jadams, 2017-07-09: added Jekyll integration
   -------------------------------------------------------------------------- */
require('./JekyllViewResults');



