/* global document                                                  */

'use strict';
module.exports = {
  compile: compile,
  setOptions: setOptions
};

var options = {};
options.pattern = /\{(.*?)\}/g;
options.template = '';
options.middleware = function () {};

function setOptions (_options) {
  options.pattern = _options.pattern || options.pattern;
  options.template = _options.template || options.template;
  if (typeof _options.middleware === 'function') {
    options.middleware = _options.middleware;
  }
}

/* jadams, 2017-07-09: Added resultsOutput for J1 Bootsnav (search)
  ------------------------------------------------------------- */
function compile(data) {
  return options.template.replace(options.pattern, function(match, prop) {
    var value = options.middleware(prop, data[prop], options.template);
    // jadams: resultsOutput hardcoded
    var resultsOutput = document.getElementById('jss-panel');
    resultsOutput.style.display = 'none'; // to hide
    if (value !== undefined) {
      return value;
    }
    // jadams: show the results container
    resultsOutput.style.display = 'block'; // to show
    return data[prop] || match;
  });
}

